.phone {
  position: relative;
  overflow: hidden;
  border: 3px solid #eee;
  border-radius: 15px;
  height: 600px;
  width: 340px;

  .content {
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    height: calc(100% - 60px);
    width: 100%;
    transition: opacity 0.4s ease;
  }

  nav {
    position: absolute;
    bottom: 0;
    left: 0;
    margin-top: -5px;
    width: 100%;

    ul {
      background-color: #fff;
      display: flex;
      list-style-type: none;
      padding: 0;
      margin: 0;
      height: 60px;

      li {
        cursor: pointer;
        flex: 1;
        padding: 10px;
        text-align: center;

        p {
          font-size: 12px;
          margin: 2px 0;
        }

        &:hover,
        &:active {
          color: #8e44ad;
        }
      }
    }
  }
}
